import React, { useState } from 'react';
import PropTypes from 'prop-types';
import './letter.css';

const DEFAULT_COLORS = `rgba(255, 255, 255, .3)`;

const Letter = ({letter, color}) => {
  const [hovered, setHovered] = useState(false);

  const style = {
    transition: hovered ? null : `color 3s 1.5s`,
    color: hovered ? color : DEFAULT_COLORS
  }

  return letter !== `` ? 
          <span onMouseOver={() => setHovered(true)} onMouseOut={() => setHovered(false)} style={style}>{letter}</span> :
          <span>{letter}</span>
};

Letter.propTypes = {
  letter: PropTypes.string.isRequired,
  color: PropTypes.string.isRequired
};


export default Letter; 