import React from 'react';
import PropTypes from 'prop-types';
import './project.css';

const TYPES = {
  new: `New!`,
  underDevelopment: 'Under Development',
  upgraded: `Upgraded!`
};

const isValidURL = (str) => {
  let pattern = new RegExp('^(https?:\\/\\/)?'+ // protocol
      '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|'+ // domain name
      '((\\d{1,3}\\.){3}\\d{1,3}))'+ // OR ip (v4) address
      '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*'+ // port and path
      '(\\?[;&a-z\\d%_.~+=-]*)?'+ // query string
      '(\\#[-a-z\\d_]*)?$','i'); // fragment locator
  return !!pattern.test(str);
}

const Project = ({name, img, location, description, type, stroke, textColor, index, length}) => {
  const style = {
    textShadow: stroke || '',
    color: textColor || null
  };

  const isUrl = isValidURL(location);

  const url = isUrl ? location : `pages/${location}/`;

  const number = index % 3;
  const additionalClass = (number === 1 || number === 2) && !(number === 1 && index === length - 1) ? `project--half` : ``;
  
  if (type === `underDevelopment` || location === `#`) {
    return (
      <article className={`project ${additionalClass}`} id={img}>
        <picture className="project__img">
          <source type="image/webp" srcSet={`img/works/${img}.webp 1x, img/works/${img}@2x.webp 2x`} className="project__img--webp" />
          <source type="image/jpeg" srcSet={`img/works/${img}.jpg 1x, img/works/${img}@2x.jpg 2x`} className="project__img--jpeg" />
          <img src={`img/works/${img}.jpg`} alt={name} />
        </picture>
  
        <div className="project__link">
          <div className="project__hover" style={style}>
            <p className={`project__type ${type ? `project__type--${type}` : ``}`}>{TYPES[type]}</p>
            <h2 className="project__title">{name}</h2>
            <p className="project__description">{description}</p>
          </div>
        </div>
      </article>
    );
  }

  return (
    <article className={`project ${additionalClass}`} id={img}>
      <picture className="project__img">
        <source type="image/webp" srcSet={`img/works/${img}.webp 1x, img/works/${img}@2x.webp 2x`} className="project__img--webp" />
        <source type="image/jpeg" srcSet={`img/works/${img}.jpg 1x, img/works/${img}@2x.jpg 2x`} className="project__img--jpeg" />
        <img src={`img/works/${img}.jpg`} alt={name} />
      </picture>

      <a href={url} className="project__link">
        <div className="project__hover" style={style}>
          <p className={`project__type ${type ? `project__type--${type}` : ``}`}>{TYPES[type]}</p>
          <h2 className="project__title">{name}</h2>
          <p className="project__description">{description}</p>
        </div>
      </a>
    </article>
  );
}

Project.propTypes = {
  name: PropTypes.string.isRequired,
  img: PropTypes.string.isRequired,
  location: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  type: PropTypes.oneOf([`underDevelopment`, `new`, `upgraded`]),
  stroke: PropTypes.string,
  textColor: PropTypes.string,
  index: PropTypes.number,
  length: PropTypes.number
}


export default Project;
