import React, { useContext } from 'react';
import './main.css';

import DataContext from '../data-context';

import Header from '../header';
import Works from '../works';
import WishLuck from '../wish-luck';

const Main = () => {
  const data = useContext(DataContext);

  return (
    <React.Fragment>
      <Header />

      <Works data={data} />

      <WishLuck />
    </React.Fragment>
  );
}


export default Main;
